import React from 'react'
import marked from "marked";
import data from "../../api/data";
import Section from '../../components/Section';
import { markedInline } from '../../utils/helpers';
import CVTable from '../../components/CVTable';

function Item(service) {
  const name = (<span className="service-name">{service.name}</span>);
  const content = (<div className="service-content">
    {Array.isArray(service.content)
      ? service.content.map((c, i) => (<div key={i} dangerouslySetInnerHTML={{__html: markedInline(c)}}/>))
      : <div dangerouslySetInnerHTML={{__html: markedInline(service.content)}}/>
    }
  </div>);
  return {name, content};
}

function Services(props) {
  const {title, services} = props;
  return (
    <Section id="services">
      <h2 dangerouslySetInnerHTML={{__html: markedInline(title)}}></h2>
      {services && (
        <CVTable className="services" items={services.map(Item)} />
      )}
        
    </Section>
  );
}


export default function DefaultServices(props) {
  const services = data.services || [];
  return <Services title='Services' services={services} {...props}/>;
}